<template>
<section>
    <loader :is-visible="isLoading"></loader>
    <div class="comapny-setup col-sm-12 col-md-12 col-lg-12 col-xl-12  float-left border px-0 pb-2">
        <Form ref="form" v-slot="{ values, meta }">
            <input type="hidden" />
            <div class="tab-content fsmtabcontrolMobileView" id="myTabContent">
                <div class="tab-pane call-detail  show active border-0 p-0" id="tabs-1" role="tabpanel" aria-labelledby="CreateProduct-tab">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">
                            Add Product Write-Off
                        </span>
                    </div>

                    <div class="container-fluid border pt-2 mb-2">
                        <div class="col-lg-12 mt-3 p-0">
                            <div class="heading-border mb-2">
                                <h5 class="h5">
                                    <span id="TabHeading1">{{ $t('Search') }}</span>
                                </h5>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="form--group mt-1">
                <div class="custom-control p-0 custom-radio mr-4 custom-control-inline">
                    <input type="radio" name="IsDocs" class="m-2" value="0" v-model="Case" v-on:change="TextfiledType(0)" />Search By Product Type
                </div>
                <div class="custom-control p-0 custom-radio mr-4 custom-control-inline">
                    Or
                </div>
                <div class="custom-contol p-0 custom-radio mr-4 custom-control-inline">
                    <input type="radio" class="m-2" name="IsDocs" value="1" v-on:change="TextfiledType(1)" /> Search by
                    Barcode/Serial Number/RFID
                </div>
            </div>

            <div class="row mt-2">

                <!-- <Search :AssetTypeData="AssetTypeData" :ProducttData="ProducttData" :LocationData="LocationData"></Search> -->

                <div id="itemsearch" class="col-12 d-flex align-items-center">
                    <div class="col-sm-12 col-md-6 col-lg-3 col-xl-2 float-left location" v-if="ProductType">
                        <div class="form-group">
                            <label>Location:<span class="text-danger">*</span></label>
                            <Field name="Location" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                <tree-view v-model="txtLocation" :options="options" v-on:input="BindItemTypeByUserId(txtLocation)" placeholder="Select Location" v-bind:class="{ 'form-control': true, 'is-invalid': isLocationInvalid }">
                                </tree-view>
                                <span v-if="isLocationInvalid" name="Location" class="invalid-feedback d-block">{{
                                        GetLocalizedValue($t("LocationIsReq")) }}
                                </span>
                            </Field>
                        </div>
                    </div>

                    <div class="col-sm-12 col-md-6 col-lg-3 col-xl-2 float-left location" v-if="ProductType">
                        <div class="form-group">
                            <label> Product Type:<span class="text-danger">*</span></label>
                            <Field name="AssetTypeData" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                <select name="AssetTypeData" v-model="txtAssetType" v-bind:class="{ 'form-control w-100': true, 'is-invalid': isAssetTypeInvalid }" @change="GetCompanyAssestCatalogs(txtAssetType)">
                                    <option value="">Select</option>
                                    <option v-for="(item, itemIndex) in AssetTypeData" :key="itemIndex" :value="item.ASSETID">{{
                                                item.ASSETTYPE }}</option>
                                </select>
                                <span v-if="isAssetTypeInvalid" name="AssetTypeData" class="invalid-feedback d-block">
                                    {{ GetLocalizedValue($t("AssetTypeIsReq")) }}

                                </span>
                            </Field>
                        </div>
                    </div>

                    <div class="col-sm-12 col-md-6 col-lg-3 col-xl-2 float-left location" v-if="ProductType">
                        <div class="form-group">
                            <label>Product Name:<span class="text-danger">*</span></label>
                            <Field name="ProductName" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                <select name="ProductName" v-model="txtProduct" @change="productNameDDLChange" v-bind:class="{ 'form-control w-100': true, 'is-invalid': istxtProductInvalid }">
                                    <option value="">Select</option>
                                    <option v-for="(item, itemIndex) in ProducttData" :key="itemIndex" :value="item.ASSET_CATALOGUE_ID">{{ item.NAME }}</option>
                                </select>
                                <span v-if="istxtProductInvalid" name="ProductName" class="invalid-feedback d-block">
                                    {{ GetLocalizedValue($t("ProductNameRequired")) }}
                                </span>
                            </Field>
                        </div>
                    </div>

                    <div class="col-sm-12 col-md-6 col-lg-3 col-xl-2 float-left location" v-if="ProductType">
                        <div class="form-group">
                            <label>Item Name:</label>
                            <Field name="Products" :rules="{ 'required': false }" label="ItemName" v-slot="{ errors }">
                                <div class="input-group mb-3" style="position: relative;">
                                    <input name="Products" id="Products" type="text" v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" v-model="itemname" value="iselectVal" />

                                    <div class="unique_dynamicdatalist">
                                        <ul v-for="(item, index) in ProductsArray" @click="setProductsName(item)" style="z-index: auto;" :key="index">
                                            <li v-if="showProductDropdown" style="text-transform: capitalize;" :value="item.Value">{{ item.Name }}</li>
                                        </ul>
                                    </div>
                                    <a class="btn float-left" href="javascript:;" style="background-color: #fff;color: #303030;border-color: #abb5bf;border-left: none;" @click="ItemListOnclick"><em class="dropdown-toggle float-right"></em></a>
                                    <span v-if="errors[0]" name="Products" class="invalid-feedback d-block">{{ errors[0]
                                        }}</span>
                                </div>

                            </Field>
                        </div>

                    </div>

                    <div class="col-sm-12 col-md-6 col-lg-3 col-xl-2 float-left location" v-if="BarCodeSerailNoRFID">
                        <div class="form-group mb-0 pt-2">

                            <input type="text" v-bind:class="{ 'form-control': true, 'is-invalid': isBarcodeInvalid }" v-model="txtBarcode" placeholder="Enter BarCode" />
                            <span v-if="isBarcodeInvalid" name="ProductName" class="invalid-feedback d-block">
                                {{ GetLocalizedValue($t("BarCodeRequired")) }}
                            </span>
                        </div>
                    </div>

                    <div class="input-group-text  border-0 bg-transparent pl-2 pb-0">

                        <a href="javascript:;" id="btnSave" class="btn btn-success " title="Save" name="btnSave" @click="ShowsearchData()" isclicked="No">Add Product </a>
                        <a v-if="BarCodeSerailNoRFID" href="javascript:;" id="btnSearch" class="btn btn-primary m-1" title="Save" name="btnsearch" isclicked="No"><i class="fa fa-barcode text-white float-left mt-1 mr-2"></i>RFID Scanned </a>
                    </div>
                </div>

            </div>
            <div class="col-sm-12">
                <div class="table-responsive mt-2 tblWriteoffItems border " style="">
                    <table id="tblWriteoffItems" class="table table-bordered dt-responsive nowrap mt-0" style="">
                        <thead class="thead-bg">
                            <tr>
                                <th>
                                    <div class="custom-control custom-checkbox">
                                        <input class="chkItems custom-control-input" v-on:click="checkboxChecked(null, 'all')" v-model="searchcheck_all" type="checkbox" id="chkAll_1" name="checkbox" style="display: none;">
                                        <label class="custom-control-label universal-custom-control-label pt-1" for="chkAll_1"></label>
                                    </div>
                                </th>
                                <th><span> {{ $t('ItemCode') }}</span></th>
                                <th><span> {{ $t('ItemName') }}</span></th>
                                <th><span> {{ $t('UniqueName') }}</span></th>
                                <th><span> {{ $t('LocationName') }}</span></th>
                                <th class="text-center"><span> {{ $t('AvailableItems') }}</span></th>
                                <th class="text-center"><span> {{ $t('PurchageValue') }}</span></th>
                                <th><span> {{ $t('CurrentbookValue') }}</span></th>
                                <th><span> {{ $t('SalavageCoast') }}</span></th>
                                <th><span> {{ $t('WriteOffValue') }}</span></th>
                            </tr>
                        </thead>
                        <tbody class="gridRowsBodyItems">
                            <tr class="trctrl">
                                <td class="text-center"></td>
                                <td class="text-center"><input id="itemCodeAutoSearch" v-model="itemCodeAutoSearch" v-on:input="SearchRecord('code')" class="form-control"></td>
                                <td class="text-center"><input id="itemNameAutoSearch" v-model="itemNameAutoSearch" v-on:input="SearchRecord('name')" class="form-control"></td>
                                <td class="align-middle"><input id="uniqueNameAutoSearch" v-model="uniqueNameAutoSearch" v-on:input="SearchRecord('unique')" class="form-control"></td>
                                <td class="text-center">
                                    <a href="javascript:;" title="Clear All" v-on:click="ClearFilter" class="clearAll btn btn-danger float-left mx-1">
                                        <i class="fa fa-refresh"></i>
                                    </a>
                                </td>
                                <td class="text-center"></td>
                                <td class="text-center"></td>
                                <td class="text-center"></td>
                                <td class="text-center"></td>
                                <td class="text-center"></td>
                            </tr>
                            <tr class="NoRecordsFound no_Search_Records_Found trctrl text-center text-danger" v-if="searchData.length == 0">
                                <td colspan="10" class="text-center text-middle text-danger no-record">
                                    {{ $t('NoRecordfound') }}
                                </td>
                            </tr>
                            <tr class="itemAddlist" v-for="(item, index) in searchData" :key="item">
                                <td class="text-center">
                                    <input v-model="item.ischecked" v-on:click="checkboxChecked(item, 'single')" type="checkbox" name="checkbox" />

                                </td>
                                <td class="text-center">{{ item.ItemCode }}</td>
                                <td class="text-center">{{ item.AssetCatalogName }}</td>
                                <td class="text-center">{{ item.AssetUniqueName }}</td>
                                <td class="text-center">{{ item.LocationName }}</td>
                                <td class="text-center single-action">
                                    <span class="text-dark"><i class="fa fa-shopping-basket" aria-hidden="true"></i><span class="noti-circle noti-blue">{{
                                                    item.AvailableItems }}</span></span>
                                </td>
                                <td class="text-center">{{ item.PurchaseValue }}</td>
                                <td class="text-center">{{ item.CurrentBookValue }}</td>
                                <td class="text-center">{{ item.SalvageValue }}</td>
                                <td>
                                    <input type="text" v-on:keypress="NumbersOnly" value="" onpaste="return false;" v-model="item.qty" data-numberonly="no" onchange="myfun(this);" v-bind:class="{ 'txtitems form-control transferQuantity': true, 'is-invalid': item.qty == '' }">
                                </td>
                            </tr>

                        </tbody>
                    </table>
                </div>
            </div>
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left my-3 Add_To_List_Button p-0" style="">
                <div class="form-group p-3">
                    <div class="search-btm-btn">
                        <a id="btn_AddRow" href="javascript:;" v-on:click="AddToList(values, meta)" class="btn btn-info formbtn float-right" value="Add to List" title="" name="btn_AddRow" data-toggle-tooltip="tooltip" data-original-title="Add To List"><i class="fa fa-plus pr-2"></i>Add To List</a>
                    </div>
                </div>
            </div>
            <div style="clear:both"></div>
            <div v-if="AddToListArray.length > 0" class="col-lg-12 mt-3 mb-3">
                <div class="heading-border mb-2">
                    <h5 class="h5"><span>{{ $t('Product Write-Off') }}</span>

                    </h5>
                </div>

                <div class="listing px-2">
                    <div class="table-responsive table-fix-header mt-3">
                        <table id="tbltransferiteminfoList" class="table table-bordered dt-responsive nowrap mt-0">
                            <thead class="thead-bg">
                                <tr>
                                    <th style="width:50px !important;" class="text-center">
                                        <input class="chkItems CheckAll" v-on:click="checkboxAddToList(null, 'all')" type="checkbox" id="chkAll" name="checkbox" />
                                    </th>
                                    <th><span> {{ $t('ItemCode') }}</span></th>
                                    <th><span> {{ $t('ItemName') }}</span></th>
                                    <th><span> {{ $t('UniqueName') }}</span></th>
                                    <th><span> {{ $t('LocationName') }}</span></th>
                                    <th class="text-center"><span> {{ $t('AvailableItems') }}</span></th>
                                    <th class="text-center"><span> {{ $t('PurchageValue') }}</span></th>
                                    <th><span> {{ $t('CurrentbookValue') }}</span></th>
                                    <th><span> {{ $t('SalavageCoast') }}</span></th>
                                    <th><span> {{ $t('WriteOffValue') }}</span></th>
                                    <th class="text-center"><span>Action</span></th>
                                </tr>
                            </thead>
                            <tbody class="gridRowsBodyItems">
                                <tr v-if="AddToListArray.length == 0" class="NoRecordsFound">
                                    <td colspan="15" class="no_records text-center text-middle text-danger no-record">
                                        {{ $t('NoRecordfound') }}</td>
                                </tr>
                                <tr class="itemAddlist" v-for="(item, index) in AddToListArray" :key="item">
                                    <td class="text-center">
                                        <input v-model="item.ischecked" v-on:click="checkboxAddToList(item, 'single')" type="checkbox" name="checkbox" />

                                    </td>
                                    <td class="text-center">{{ item.ItemCode }}</td>
                                    <td class="text-center">{{ item.AssetCatalogName }}</td>
                                    <td class="text-center">{{ item.AssetUniqueName }}</td>
                                    <td class="text-center">{{ item.LocationName }}</td>
                                    <td class="text-center single-action">
                                        <span class="text-dark"><i class="fa fa-shopping-basket" aria-hidden="true"></i><span class="noti-circle noti-blue">{{
                                                        item.AvailableItems }}</span></span>
                                    </td>
                                    <td class="text-center">{{ item.PurchaseValue }}</td>
                                    <td class="text-center">{{ item.CurrentBookValue }}</td>
                                    <td class="text-center">{{ item.SalvageValue }}</td>
                                    <td>
                                        <input type="text" v-on:keypress="NumbersOnly" value="" onpaste="return false;" v-model="item.qty" data-numberonly="no" v-bind:class="{ 'txtitems form-control transferQuantity': true, 'is-invalid': item.qty == '' }">
                                    </td>
                                    <td class="text-center single-action">
                                        <a href="javascript:;" v-on:click="DeleteRow(index)" title="Delete"><i class="fa fa-trash text-danger action-icon"></i></a>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <div class="col-md-12 col-lg-12 col-xl-12 float-left mt-3 mb-2 text-right p-0">
                    <a href="javascript:;" id="btnSave" v-on:click="onSubmit" class="btn btn-success " title="Save" name="btnSave" isclicked="No"><i class="fa fa-save pr-2"></i>Save </a>
                    <a id="btnCancel" v-on:click="onCancel" href="/AssetCatalog/ItemWriteOffList" title="Cancel" class="btn btn-danger HideTab m-1"><i class="fa fa-close pr-2"></i>Cancel</a>
                </div>

            </div>

            <div class="col-lg-12 text-right mt-3 float-left">

            </div>
        </Form>
    </div>
</section>
</template>

<script>
import DataService from '../../service/DataService';
import Search from '../../components/Modal/SearchAsset.vue';
import Treeselect from '@riophae/vue-treeselect'
import Pager from '../../components/Common/Pager.vue'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
export default {
    components: {
        Search,
        Pager,
        'tree-view': Treeselect
    },
    data() {

        return {
            Case: 0,
            ProductType: true,
            BarCodeSerailNoRFID: false,
            Headers: [{
                    "COLUMN_NAME": "Product Code",
                    "DATA_TYPE": "Text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Product Code",
                    "DISPLAY_ORDER": 1,

                },
                {
                    "COLUMN_NAME": "Product Name",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ProductName'),
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "Unique Name",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Unique Name",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "Location",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Location",
                    "DISPLAY_ORDER": 4,

                },
                {
                    "COLUMN_NAME": "Available Quantity",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Available Quantity",
                    "DISPLAY_ORDER": 5,

                },
                {
                    "COLUMN_NAME": "Purchase Value",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Purchase Value",
                    "DISPLAY_ORDER": 6,

                },

                {
                    "COLUMN_NAME": "Current book Value",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Current book Value",
                    "DISPLAY_ORDER": 7,

                },
                {
                    "COLUMN_NAME": "Salvage Cost",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Salvage Cost",
                    "DISPLAY_ORDER": 8,
                },
                {
                    "COLUMN_NAME": "Write-Off Value",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Write-Off Value",
                    "DISPLAY_ORDER": 9,
                },

            ],
            LegendArray: [],
            ProductData: [],
            PageSize: 10,
            PageNumber: 1,
            PageSizeOption: null,
            txtLocation: null,
            ProducttData: [],
            Groupname: '',
            LocationData: [],
            AssetTypeData: [],
            txtAssetType: '',
            isLocationInvalid: false,
            isAssetTypeInvalid: false,
            istxtProductInvalid: false,
            UserId: null,
            txtProduct: '',
            txtItemName: '',
            UserId: null,
            itemname: '',
            ProductsArray: [],
            showProductDropdown: false,
            iselectVal: '',
            options: [],
            searchData: [],
            barcode: null,
            searchcheck_all: false,
            finalcheck_all: false,
            //TotalRecords: null,
            TotalRecord: 0,
            AddToListArray: [],
            itemCodeAutoSearch: '',
            itemNameAutoSearch: '',
            uniqueNameAutoSearch: '',
            firstListArray: [],
            txtBarcode: '',
            isBarcodeInvalid: false,
            isLoading: false,
            writeOffSelect: [],
            selectAll: false,
            CurrentPage: 1,
            TotalPage: 0,
            noRecord: false
        }

    },

    created: function () {
        var vm = this;
        this.GetLocationByuserId();
        this.BindItemTypeByUserId();
        this.GetCompanyAssestCatalogs();
        this.UserId = this.GetUserInfo.ID;
        vm.isLocationInvalid = false;
        vm.isAssetTypeInvalid = false;
    },

    methods: {
        // 1. Dropdowns start
        GetLocationByuserId: function () {
            var vm = this;
            DataService.GetLocationByuserId().then(response => { // this.LocationData = response.data.data;
                vm.FindalLocationArray = [];
                response.data.data.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });
                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            let locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, response.data.data)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });
                vm.options = vm.FindalLocationArray;
            });
        },
        checkboxAddToList: function (itm, type) {
            var vm = this;
            if (type == 'all') {
                if ($('#chkAll_0').prop('checked')) {
                    vm.AddToListArray.forEach((itm) => {
                        itm.ischecked = true;
                    })
                } else {
                    vm.AddToListArray.forEach((itm) => {
                        itm.ischecked = false;
                    })
                }
            } else {
                itm.ischecked = !itm.ischecked
                if (itm.ischecked) {
                    $('#chkAll_1').prop('checked', true)
                    vm.AddToListArray.forEach((item) => {
                        if (item.ischecked) {
                            $('#chkAll_1').prop('checked', true)
                        }
                    })
                } else {
                    $('#chkAll_1').prop('checked', true)
                    vm.AddToListArray.forEach((item) => {
                        if (!item.ischecked) {
                            $('#chkAll_1').prop('checked', false)
                        }
                    })
                }
            }
        },
        checkboxChecked: function (itm, type) {
            var vm = this;
            if (type == 'all') {
                if ($('#chkAll_0').prop('checked')) {
                    vm.searchData.forEach((itm) => {
                        itm.ischecked = true;
                    })

                } else {
                    vm.searchData.forEach((itm) => {
                        itm.ischecked = false;
                    })
                }
            } else {
                itm.ischecked = !itm.ischecked
                if (itm.ischecked) {
                    $('#chkAll_0').prop('checked', true)
                    vm.searchData.forEach((item) => {
                        if (!item.ischecked) {
                            $('#chkAll_0').prop('checked', false)
                        }
                    })
                } else {
                    vm.searchData.forEach((item) => {
                        if (!item.ischecked) {
                            $('#chkAll_0').prop('checked', false)
                        }
                    })
                }

            }

        },
        NumbersOnly(evt) {
            evt = (evt) ? evt : window.event;
            var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                evt.preventDefault();;
            } else {
                return true;
            }
        },
        async AddToList() {
            var vm = this;
            var msg = "";
            var count = 1;
            var chk = true;
            var chkExistName = "";
            var tempArray = [];
            var isItem = vm.searchData.filter(x => x.ischecked);
            if (isItem.length > 0) {
                vm.searchData.forEach((value, index) => {
                    // RelatedInfoId
                    vm.AddToListArray.forEach((itm) => {
                        if (itm.RelatedInfoId === value.RelatedInfoId) {
                            chkExistName += itm.AssetUniqueName;
                            const indexToRemove = vm.searchData.findIndex(item => item.RelatedInfoId === value.RelatedInfoId);
                            // Use splice to remove the element at the found index
                            if (indexToRemove !== -1) {
                                vm.searchData.splice(indexToRemove, 1);
                            }
                        }
                    });
                    if (value.ischecked) {
                        if (value.qty === '') {
                            vm.ShowAlert(vm.$t('CorrectHighlightedErrors'), "warning", true, vm.$t('Alert'));
                            chk = false;
                        } else {
                            // value.isCheckedAddtolist = false;
                            // value.qtyAddtolist = '';
                            tempArray.push(value);

                            // If value is pushed to tempArray, remove it from vm.searchData
                            const indexToRemove = vm.searchData.findIndex(item => item.RelatedInfoId === value.RelatedInfoId);
                            if (indexToRemove !== -1) {
                                vm.searchData.splice(indexToRemove, 1);
                            }
                        }
                    }
                });
            } else {
                vm.ShowAlert(vm.$t('MinimumOneSelect'), "warning", true, vm.$t('Alert'));
                chk = false;
            }

            if (chkExistName.length > 0) {
                vm.ShowAlert(chkExistName + ' Already Exist', "warning", true, vm.$t('Alert'));
                chk = false;
            }
            if (chk) {
                // vm.searchData = [];
                vm.firstListArray = [];
                tempArray.forEach((value) => {
                    vm.AddToListArray.push(value);
                    vm.txtLocation = null;
                    vm.txtAssetType = '';
                    vm.txtProduct = '';
                    vm.itemname = '';
                    $('#chkAll_0').prop('checked', false);
                    vm.$refs.form.reset();
                });
                setTimeout(function () {
                    vm.ResponsiveDataTable('tbltransferiteminfoList');
                    vm.isLocationInvalid = false;
                }, 500);
                setTimeout(function () {
                    vm.CheckBoxBootstrap();
                    $('.CheckAll').prop('checked', true);
                }, 100);
            }
        },
        async DeleteRow(index) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("ASSET_CATALOGUE_ID")), function () {
                vm.AddToListArray.splice(index, 1);
            })
        },
        async ShowsearchData() {
            let vm = this;
            var url = null;
            vm.isLoading = true;
            if (!vm.BarCodeSerailNoRFID) {
                if (vm.txtProduct == '' || vm.txtAssetType == '' || vm.txtLocation == '') {
                    vm.isLocationInvalid = (vm.txtLocation == '' || vm.txtLocation == null) ? true : false;
                    vm.isAssetTypeInvalid = vm.txtAssetType == '' ? true : false;
                    vm.istxtProductInvalid = vm.txtProduct == '' ? true : false;

                    vm.ShowAlert(vm.$t("CorrectHighlightedErrors"), "warning", vm.$t('Alert'));
                    vm.isLoading = false;
                    return;
                } else {
                    vm.isLocationInvalid = false;
                    vm.isAssetTypeInvalid = false;
                    vm.istxtProductInvalid = false;
                }
                url = `id=${vm.txtProduct}&isView=1&assetType=${vm.txtAssetType}&barcode=&assetUniqueId=${vm.iselectVal}&selfCheckout=&reqFrom=writeoff&isFor=search&assignedFrom=&assignedTill=&startTime=&endTime=&locationId=${vm.txtLocation}&pageNum=1`;
            } else {
                if (vm.txtBarcode == '') {
                    vm.isBarcodeInvalid = (vm.txtBarcode == '' || vm.txtBarcode == null) ? true : false;
                    vm.ShowAlert(vm.$t("CorrectHighlightedErrors"), "warning", vm.$t('Alert'));
                    vm.isLoading = false;
                    return;
                } else {
                    vm.isBarcodeInvalid = false;
                }
                url = `id=&isView=1&assetType=&barcode=${vm.txtBarcode}&assetUniqueId=&selfCheckout=&reqFrom=writeoff&isFor=search&assignedFrom=&assignedTill=&startTime=&endTime=&locationId=0&pageNum=1`;
            }

            await DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {
                vm.searchData = [];
                if (response.data.length > 0) {
                    vm.firstListArray = [];
                    vm.searchData = [];
                    vm.ClearFilter();
                    response.data.forEach((value) => {
                        value.ischecked = false;
                        value.qty = '';
                        vm.searchData.push(value);
                        vm.firstListArray.push(value);
                        vm.isLoading = false;

                    });
                    vm.isLoading = false;
                    setTimeout(function () {
                        vm.ResponsiveDataTable('tblWriteoffItems')
                    }, 300);
                    setTimeout(function () {
                        vm.CheckBoxBootstrap();
                    }, 100);
                    vm.isLoading = false;
                }
                vm.isLoading = false;

            });
        },
        async SearchRecord(type) {
            var vm = this;
            var tempArrayList = [];
            if (typeof (type) == 'undefined') {
                return false;
            }
            if (type == 'code') {
                vm.searchData = vm.firstListArray.filter(item => {
                    return item.ItemCode.toLowerCase().indexOf(vm.itemCodeAutoSearch.toLowerCase()) > -1
                })
            } else if (type == 'name') {
                vm.searchData = vm.firstListArray.filter(item => {
                    return item.AssetCatalogName.toLowerCase().indexOf(vm.itemNameAutoSearch.toLowerCase()) > -1
                })
            } else {
                vm.searchData = vm.firstListArray.filter(item => {
                    return item.AssetUniqueName.toLowerCase().indexOf(vm.uniqueNameAutoSearch.toLowerCase()) > -1
                })
            }
            setTimeout(function () {
                vm.ResponsiveDataTable('tblWriteoffItems')
            }, 300);
            setTimeout(function () {
                vm.CheckBoxBootstrap()
            }, 100);

        },
        async ClearFilter() {
            var vm = this;
            vm.itemCodeAutoSearch = '';
            vm.itemNameAutoSearch = '';
            vm.uniqueNameAutoSearch = '';
            vm.SearchRecord();
        },
        onSubmit: async function (formfields) {
            var vm = this;
            vm.isLoading = true;
            var msg = "";
            var MoveToInfo = "";
            var count = 1;
            var chk = false;
            var totalSelectedQuantity = 0;
            var i = 0;
            var assetCatId = 0;
            vm.AddToListArray.forEach((value) => {
                if (value != null) {
                    if (value.ischecked) {
                        chk = true;
                        if (i == 0) {
                            assetCatId = value.AssetCatalogueId
                        }
                        var rid = value.RelatedInfoId;
                        var qty = value.qty; // item.qtyAddtolist;                        //validation
                        if (qty == 0) {
                            msg += "no";
                        }
                        MoveToInfo += rid + ',' + qty + '|';
                        totalSelectedQuantity += qty;
                        i++;
                    }
                }
            })
            if (chk && msg.length == 0) {
                const RaiseData = {
                    AssetCatalogueId: parseInt(assetCatId),
                    ItemName: '',
                    ItemGroupName: '',
                    ItemType: '',
                    Quantity: 0,
                    RelatedInfoId: 0,
                    LocationId: 0,
                    LocationName: '',
                    NumberOfItems: 0,
                    UniqueName: '',
                    Description: '',
                    AvailableItems: 0,
                    CreateUniqueNameListing: 0,
                    ItemCode: '',
                    MoveToInfo: MoveToInfo,
                    ParentLocationId: 0
                }
                var Jsondata = JSON.stringify(RaiseData);
                await DataService.UpdateWriteOffSave(RaiseData).then(response => {

                    if (response.data.result == 'success') {
                        vm.ShowAlert(vm.$t('WriteOffSuccessfully'), "success", vm.$t('Alert'));
                        vm.searchData = [];
                        vm.AddToListArray = []
                        vm.isLoading = false;
                    } else {
                        vm.ShowAlert(vm.$t('WriteOffError'), "warning", vm.$t('Alert'));
                        vm.isLoading = false;
                    }
                });
            } else {
                if (!chk) {
                    vm.ShowAlert(vm.$t('MinimumOneSelect'), "warning", true, vm.$t('Alert'));
                    vm.isLoading = false;
                } else if (msg.length > 0) {
                    vm.ShowAlert(vm.$t('CorrectHighlightedErrors'), "warning", true, vm.$t('Alert'));
                    vm.isLoading = false;
                }
            }

        },
        async checkBoxClick(singleItem, type, searchcheck_all, tabletype) {
            var vm = this;
            if (tabletype == 'searchtbl') {
                //singleItem.ischecked = !singleItem.ischecked;
                if (type == "single") {
                    vm.searchData.forEach((value) => {
                        if (!value.ischecked) {
                            vm.searchcheck_all = false;
                        }
                    });
                } else {
                    if (!searchcheck_all) {
                        vm.searchData.forEach((value) => {
                            value.ischecked = true;
                        });
                    } else {
                        vm.searchData.forEach((value) => {
                            value.ischecked = false;
                        });
                    }
                }
            } else {
                //singleItem.ischecked = !singleItem.ischecked;
                if (type == "single") {
                    vm.AddToListArray.forEach((value) => {
                        if (!value.ischecked) {
                            vm.finalcheck_all = false;
                        }
                    });
                } else {
                    if (!finalcheck_all) {
                        vm.AddToListArray.forEach((value) => {
                            value.ischecked = true;
                        });
                    } else {
                        vm.AddToListArray.forEach((value) => {
                            value.ischecked = false;
                        });
                    }
                }
            }
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },
        BindItemTypeByUserId: function (ths) {
            if(typeof(ths)=='undefined' || ths==''){
                return false;
            }
            else{
                this.isLoading = true;
                let url = `type=ITEMCODE&issueTo=${this.UserId}&locationId=${ths}&requestType=ASSIGN&requestFrom=WriteOff`;
                DataService.AssignGetAssetType(url).then(response => {
                    this.AssetTypeData = response.data;
                    this.isLoading = false;
                    this.isLocationInvalid = (this.txtLocation == '' || this.txtLocation == null) ? true : false;
                });
                this.isLocationInvalid = (this.txtLocation == '' || this.txtLocation == null) ? true : false;
                this.isLoading = false;
            }
            
        },
        GetCompanyAssestCatalogs: function (ths) {
            if(typeof(ths)=='undefined' || ths==''){
                return false;
            }
            else{
                this.isLoading = true;
                let url = `id=${ths}&issueTo=${this.UserId}&locationId=${this.txtLocation}&requestFrom=assetassignment`;
                DataService.GetCompanyAssestCatalogs(url).then(response => {
                    this.ProducttData = response.data;
                    this.isLoading = false;

                    this.isAssetTypeInvalid = this.txtAssetType == '' ? true : false;
